import axios from "axios";

axios.AxiosHeaders = { Authorization: 'token ' + process.env.REACT_APP_GITHUB_API_TOKEN };

const url = "https://api.github.com"

export default class GithubAPI {
	static async getReposAmount() {
		const response = await axios.get(url + "/user/repos", {headers: axios.AxiosHeaders})
		return response.data.length || 0;
	}

	static async getCommits() {
		const responseRepos = await axios.get(url + "/user/repos", {headers: axios.AxiosHeaders})
		const repos = responseRepos.data;

		const array = repos.map(async (repo) => {
			const response = await axios.get(url + `/repos/${repo.full_name}/commits`, {headers: axios.AxiosHeaders})
			return response.data;
		});

		const values = await Promise.all(array)
		
		const result = [];
		values.forEach(value => result.push(...value))
		
		const ret = result.filter( commit => commit.author.login === "ElRataAlada")

		return (ret.length + repos.length) || 0;
	}
}